.Modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.16);
}
.Modal {
  position: fixed;
  top: 50%;
  left: 50%;
  z-index: 1;
  transform: translate(-50%, -50%);
  width: 500px;
  border-radius: 10px;
  background-color: white;
  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16);
}
.Modalsmall {
  position: fixed;
  top: 50%;
  left: 50%;
  z-index: 1;
  line-height: 5vw;
  font-size: 5vw;
  transform: translate(-50%, -50%);
  width: 20vw;
  border-radius: 10px;
  background-color: white;
  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16);
}

.Modal a {
  color: white;
}

